/* ----------------------------------- COLORS ----------------------------------- */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-family: "Raleway", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: hsl(243deg, 100%, 93%);
}

body {
  background-image: url(../img/bg-mobile.png);
  background-size: cover;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

main {
  width: calc(100% - 50px);
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
main .fylo-card, main .stats-card {
  box-shadow: 2px 5px 10px 0px hsla(0deg, 0%, 0%, 0.1);
}
main .fylo-card {
  background-color: hsl(228deg, 56%, 26%);
  padding: 3rem;
  border-radius: 0.7rem 6rem 0.7rem 0.7rem;
}
main .fylo-card h1 {
  margin-bottom: 1.5rem;
}
main .fylo-card .buttons-container {
  display: flex;
  align-items: center;
  gap: 1.2rem;
}
main .fylo-card .buttons-container a {
  cursor: pointer;
  background-color: hsl(229deg, 57%, 11%);
  width: 3.4rem;
  height: 3.4rem;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.2s ease-in-out;
}
main .fylo-card .buttons-container a:hover {
  background-color: #121a40;
}
main .stats-card {
  background-color: hsl(228deg, 56%, 26%);
  padding: 2.5rem 2rem 4rem 2rem;
  border-radius: 0.7rem;
  position: relative;
}
main .stats-card p {
  text-align: center;
}
main .stats-card p span {
  font-weight: 700;
}
main .stats-card .bar-empty {
  width: 100%;
  height: 1.4rem;
  background-color: #121a40;
  margin: 1rem 0 0.5rem;
  border-radius: 10rem;
  border: 3px solid #121a40;
}
main .stats-card .bar-empty .bar-current {
  width: 81.5%;
  height: 100%;
  background: linear-gradient(to right, hsl(6deg, 100%, 80%), hsl(335deg, 100%, 65%));
  border-radius: 10rem;
  position: relative;
  display: flex;
  align-items: center;
}
main .stats-card .bar-empty .bar-current::after {
  content: "";
  height: 70%;
  aspect-ratio: 1/1;
  background-color: hsl(0deg, 0%, 100%);
  position: absolute;
  right: 0;
  transform: translateX(-15%);
  border-radius: 50%;
}
main .stats-card .storage-min-max {
  display: flex;
  justify-content: space-between;
  font-weight: 700;
  font-size: 0.9rem;
}
main .stats-card .storage-remaining {
  background-color: hsl(0deg, 0%, 100%);
  padding: 0.8rem 1.8rem;
  color: hsl(229deg, 57%, 11%);
  font-weight: 700;
  border-radius: 0.7rem;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 180px;
}
main .stats-card .storage-remaining p:nth-child(1) {
  font-size: 3rem;
}
main .stats-card .storage-remaining p:nth-child(2) {
  font-size: 0.9rem;
  color: hsl(229deg, 7%, 55%);
}

@media screen and (min-width: 768px) {
  body {
    background-image: url(../img/bg-desktop.png);
    background-color: hsl(229deg, 57%, 11%);
    background-size: auto;
    background-repeat: no-repeat;
    background-position: bottom;
  }
  main {
    max-width: 920px;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    position: relative;
  }
  main .fylo-card {
    width: 100%;
    max-width: 350px;
  }
  main .stats-card {
    width: 100%;
    max-width: 540px;
    padding-bottom: 3.3rem;
    position: static;
  }
  main .stats-card p {
    text-align: left;
  }
  main .stats-card .storage-remaining {
    right: 0;
    top: 0;
    left: auto;
    bottom: auto;
    transform: translate(-2rem, 0);
    border-bottom-right-radius: 0;
  }
  main .stats-card .storage-remaining::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    transform: translateY(100%);
    border-bottom: 1.5rem solid transparent;
    border-right: 1.5rem solid hsl(0deg, 0%, 100%);
  }
}
@media screen and (min-width: 1440px) {
  body {
    background-size: contain;
  }
}